body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
  width: 90%;
  margin: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header {
  text-align: center;
}

.navbar {
  top: 0;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  position: sticky;
  position: -webkit-sticky; /* Safari */
  list-style-type: none;
  background-color: #333;
}

.navbar li {
  display: inline-block;
  width: 33.333333%;
}

.navbar li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.active {
  background-color: #475ab1;
}

@media only screen and (min-width: 768px) {
  .navbar li a:hover {
    background-color: #111;
  }
}

.story-heading a {
  text-decoration: none;
  color: #111;
}

.comment-section {
  font-size: 0.8em;
}

.comment {
  padding: 15px 0 5px 15px;
  text-align: left;
  margin: 10px auto;
  list-style-type: none;
  box-shadow: 2px 1px 1px 1px #ccc;
}

.comment h4 {
  padding-right: 5px;
  padding-bottom: 5px;
  font-style: italic;
  display: inline-block;
  text-decoration: underline;
}

.comment-sub {
  border-left: 2px solid #111;
}

@media only screen and (min-width: 768px) {
  .story-heading a {
    color: #333;
  }

  .story-heading a:hover {
    text-decoration: underline;
    color: #111;
  }

  .comment-sub {
    border-left: 2px solid #ccc;
  }

  .comment-sub:hover {
    border-left: 2px solid #111;
  }
}

.stories-list {
  text-align: left;
  margin: 25px auto;
  list-style-type: none;
  box-shadow: 1px 1px 1px 1px #ccc;
}

.story {
  width: 100%;
  display: table;
  padding: 10px 20px;
  box-sizing: border-box;
  border-bottom: 1px solid #ccc;
}

.stories-list a {
  color: inherit;
  text-decoration: none;
}

.no-items {
  margin: 0 auto;
  padding: 25px;
  color: rgb(200, 200, 200);
}

.story-detail {
  font-size: 10px;
}

button {
  padding: 2px;
  margin: 0 5px;
  cursor: pointer;
  background-color: #ccc;
}

@media only screen and (min-width: 768px) {
  .story:hover {
    background-color: #ccc;
  }

  .stories-list a:hover p {
    text-decoration: underline;
  }
}

